body, html {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-color: rgb(88, 173, 173);
}

.app {
  background-color: white;
  border-radius: 5px;
  height: 70vh;
  width: 50vw;
  padding: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: 'Roboto Flex', sans-serif;
  box-shadow: 1px 1px 30px black;
}

.form-input {
  display: flex;
  flex-direction: column;
  width: 40%;
  height: 22%;
}

.btn-submit {
  width: 50%;
  align-self: center;
}

.input-resultado {
  display: flex;
  flex-direction: column;
  width: 40%;
}

.app-resources a {
  color: rgb(97, 22, 112);
}

.item-contact {
  font-size: 40px;
}

.app-footer {
  height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.app-footer ul {
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  width: 30%;
  margin-top: -10px;
}

.app-footer li {
  margin-left: -40px;
}

.app-footer a {
  text-decoration: none;
  color: black;
}